import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
const GuardianName = (props) => {
    const taxStatus = props.location.state?.taxStatus

    const [name,setName]=React.useState('');
    const [nextDisabled,setNextDisabled]=React.useState(false)

    React.useEffect(() => {
        if(!props.location.state?.taxStatus){
			props.history.push('/activate-direct-mf/tax-status')
		} else {
            db.get(db.model.userData).then((u) => {
                if (u) {
                    setName(u.GuardianName || props.location.state?.nameOnPan || '')
                }
            })
        }
	}, []);
    React.useEffect(() => {
		checkValidation();
	}, [name]);
    const checkValidation = () => {
            if (name.length >= 2 ) {
                setNextDisabled(false);
            } else {
                setNextDisabled(true);
            }  
    };

    return (
        <div className='mainContainer'>
            <MainHeader
                header="Activate mutual funds"
                navigation={props.history}
            />
            <ProgressBar now={45} className="progress-bar-custom" />
            <div className="dataContainer" style={{paddingTop:'42px'}}>
                <div className="bodyTextDark">Enter legal guardian name (as per PAN card)</div>
                <input
                    className="normalInput"
                    placeholder="Vijay Sharma"
                    style={{textTransform:'capitalize'}}
                    type="text"
                    id="Full-name"
                    value={name}
                    onChange={(text) => {
                        const newName = text.target.value.replace(/[^a-zA-Z\s]/g, '');
                        setName(newName);
                    }}
                />
            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    nextDisabled={nextDisabled}
                    navigation={props.history}
                    buttonText="Confirm"
                    next="/activate-direct-mf/guardian-DOB"
                    params={props.location.state}
                    saveData={{
                        GuardianName:name
                    }}/>
            </div>
        </div>
    )
}

export default GuardianName
